import React from 'react';
import { Routes, Route } from 'react-router-dom';
import Main from './Main';

const RouterMain = () => {
    const all_tasks = {
        type: 'all_tasks',
        title: 'All Tasks',
    };
    const daily_tasks = {
        type: 'daily_tasks',
        title: 'Daily Tasks',
    };
    const completed_all_tasks = {
        type: 'completed_all_tasks',
        title: 'All Completed Tasks',
    };
    const completed_daily_tasks = {
        type: 'completed_daily_tasks',
        title: 'Completed Daily Tasks',
    };
    return (
        <Routes>
            <Route path='/' element={<Main {...all_tasks} />} />
            <Route path='/daily_tasks' element={<Main {...daily_tasks} />} />
            <Route
                path='/all_tasks_completed'
                element={<Main {...completed_all_tasks} />}
            />
            <Route
                path='/daily_tasks_completed'
                element={<Main {...completed_daily_tasks} />}
            />
        </Routes>
    );
};

export default RouterMain;
