import './Aside.css';
import { NavLink } from 'react-router-dom';

import { useTaskContext } from '../../../../contexts/TasksContext';

import { BsSearch } from 'react-icons/bs';

function Aside() {
    const { setTextSearch, setSelectPriority } = useTaskContext();

    const handleChange = (e) => {
        setTextSearch(e.target.value);
    };

    const handleSelectPriority = (e) => {
        setSelectPriority(e.target.value);
    };
    return (
        <div className='section-aside col-3'>
            <label className='search'>
                <input
                    type='text'
                    placeholder='Searchâ€¦'
                    onChange={(e) => handleChange(e)}
                />
                <BsSearch />
            </label>
            <div className='cards-links'>
                <NavLink
                    to='/'
                    className={({ isActive, isPending }) =>
                        isPending ? 'pending' : isActive ? 'active' : ''
                    }
                >
                    All Tasks
                </NavLink>
            </div>
            <div className='cards-links'>
                <NavLink
                    to='/daily_tasks'
                    className={({ isActive, isPending }) =>
                        isPending ? 'pending' : isActive ? 'active' : ''
                    }
                >
                    Daily Tasks
                </NavLink>
            </div>
            <div className='cards-links'>
                <NavLink
                    to='/all_tasks_completed'
                    className={({ isActive, isPending }) =>
                        isPending ? 'pending' : isActive ? 'active' : ''
                    }
                >
                    All Completed Tasks
                </NavLink>
            </div>
            <div className='cards-links'>
                <NavLink
                    to='/daily_tasks_completed'
                    className={({ isActive, isPending }) =>
                        isPending ? 'pending' : isActive ? 'active' : ''
                    }
                >
                    Completed Daily Tasks
                </NavLink>
            </div>
            <div className='cards-links select-priority'>
                <h6>select priority</h6>
                <select name='priority' onChange={handleSelectPriority}>
                    <option value='all' defaultValue>
                        all
                    </option>
                    <option value='urgent'>urgent</option>
                    <option value='high'>high</option>
                    <option value='normal'>normal</option>
                    <option value='low'>low</option>
                </select>
            </div>
        </div>
    );
}

export default Aside;
